.page-footer {
  background-color: $color-black-pearl;
  min-height: 280px;
  padding: 50px 0;
}

.page-footer__wrapper {
  display: flex;
  gap: 160px;
  max-width: 1310px;

  @include vp-1439 {
    flex-direction: column;
    gap: 35px;
  }

  @include vp-1023 {

  }

  @include vp-767 {
    gap: 0;
  }
}

.page-footer__inner-social-logo {
  display: flex;
  flex-direction: column;
  gap: 30px;

  @include vp-1439 {
    flex-direction: row;
    gap: 30px;
    justify-content: space-between;
  }

  @include vp-1023 {

  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    padding-bottom: 25px;
  }
}

.page-footer__inner-nav-contacts {
  display: flex;
  gap: 30px;

  @include vp-1439 {
    gap: 130px;
    justify-content: space-between;
  }

  @include vp-1023 {
    gap: 130px;
    justify-content: space-between;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 0;
  }
}

.page-footer__logo img {
  width: 215px;
  height: 55px;

  @include vp-767 {
    width: 215px;
    height: 55px;
  }
}

.is-active {

  @include vp-767 {
    &::after {
      transform: rotate(0);
    }
  }
}

.is-open ul {

  @include vp-767 {
    display: grid;
  }
}
